import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Sheet300Service} from '../sheet300.service';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {resolveSrv} from 'dns';
import { NotifierService } from 'src/app/notifier.service';

@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.css']
})
export class DialogComponent implements OnInit, OnDestroy {
  picker1: any;
  picker2: any;
  mdfir300Form: FormGroup;
  actionsBtn = 'Save';

  constructor(private formBuilder: FormBuilder,
              private sheet300Service: Sheet300Service,
              private notifierService: NotifierService,
              @Inject(MAT_DIALOG_DATA) public editData: any,
              // tslint:disable-next-line:no-shadowed-variable
              private MatDialogRef: MatDialogRef<DialogComponent>) { }

  ngOnInit(): void {
    this.mdfir300Form = this.formBuilder.group({
      code: [''],
      description: ['' ],
      amount: ['', Validators.required],
    });
    if (this.editData){
      this.actionsBtn = 'Update';
      // tslint:disable-next-line:no-unused-expression
      console.log(this.editData.code)
      this.mdfir300Form.controls.code.setValue(this.editData.code);
      this.mdfir300Form.controls.description.setValue(this.editData.description);
      this.mdfir300Form.controls.amount.setValue(this.editData.amount);
    }
  }

  ngOnDestroy(): void {
  }

  addDetails(): void {
    if (!this.editData){
      if (this.mdfir300Form.valid) {
        this.sheet300Service.postMDFIR300Data(this.mdfir300Form.value).subscribe(
          {
            next: (res) => {
              this.notifierService.showNotification('MDFIR300 updated successfully', 'OK', 'success');
              this.mdfir300Form.reset();
              this.MatDialogRef.close('save');
            },
            error: () => {
              this.notifierService.showNotification('Unable to add new row', 'OK', 'error');
            }}
        );
      }
    } else {
      this.putDetails();
  }
    // tslint:disable-next-line:typedef
    console.log(this.mdfir300Form.value);
}

  // tslint:disable-next-line:typedef
  private putDetails(): void {
    this.sheet300Service.putMDFIR300Data(this.editData.code, this.mdfir300Form.value).subscribe({
      next: (response) => {
        this.notifierService.showNotification('MDFIR300 updated successfully', 'ok', 'success');
        console.log(this.mdfir300Form.value);
        this.mdfir300Form.reset();
        this.MatDialogRef.close('update');
      },
      error: () => {
        this.notifierService.showNotification('Error updating Report', 'OK', 'error');
      }
    });
  }
  }

